import React, {memo, useCallback, useEffect} from 'react';
import {Avatar, Comment, FullScreenWrapper, TimeAgo} from '@true/ui';
import {
  Fonts,
  goBack,
  styled,
  useAsyncFn,
  useNavigationParams,
  useUser,
} from '@true/core';
import Animated, {
  Extrapolation,
  interpolate,
  useAnimatedStyle,
  useSharedValue,
  useWorkletCallback,
} from 'react-native-reanimated';
import {
  Image,
  NativeScrollEvent,
  NativeSyntheticEvent,
  View,
} from 'react-native';
import {IC_OUTLINED_LEFT, IC_OUTLINED_MORE_HORIZ} from '@true/asset';
import {useAnimatedSafeAreaInsets} from 'react-native-safe-area-context';
import {globalStyles} from '@/utils/globalStyle';
import {PostThumbnail} from '@/components/PostThumbnail';
import {useUpdates} from '@/store/updates';
import TypePost from '@/services/TypePostService';
import {FlexCenter, STextDivider} from '@/utils/helper';
import {requestUpdatePost} from '@/store/updates/function';
import {setActiveUpdate} from '@/zustand/activeUpdate';
import {openActionPostScreen} from '@/utils';

export interface DetailPostScreenParams {
  user_id: number;
  time: number;
  id: number;
  hid: string;
}

export const DetailPostScreen = memo(function () {
  const {user_id, time, id, hid} =
    useNavigationParams<DetailPostScreenParams>();
  const user = useUser(user_id);
  const data = useUpdates(id);
  const {aTop} = useAnimatedSafeAreaInsets();
  const animatedPaddingTop = useAnimatedStyle(() => ({
    paddingTop: aTop.value,
  }));
  const scroll_x = useSharedValue(0);

  const onContentScroll = useWorkletCallback(
    (event: NativeSyntheticEvent<NativeScrollEvent>) => {
      const scrollValue = event.nativeEvent.contentOffset.y;
      scroll_x.value = scrollValue;
    },
    [],
  );

  const animatedTextHeaderStyle = useAnimatedStyle(() => {
    const info_header_opacity = interpolate(scroll_x.value, [0, 120], [0, 1], {
      extrapolateRight: Extrapolation.CLAMP,
    });
    return {
      opacity: info_header_opacity,
    };
  });

  const openActionUpdate = useCallback(() => {
    openActionPostScreen();
    setActiveUpdate(data?.truetype, id, user_id, hid);
  }, []);

  const [{loading: firstLoading, error: firstError}, loadFirst] = useAsyncFn(
    () => requestUpdatePost({update_id: id}),
    [id],
  );
  const [{loading: refreshing, error: refreshError}, refresh] = useAsyncFn(
    () => requestUpdatePost({update_id: id}),
    [id],
  );

  useEffect(() => {
    loadFirst().then();
  }, []);

  return (
    <FullScreenWrapper>
      <Comment.Provider
        appName={'inside'}
        type={'comment'}
        hid={data?.attachment?.hid || data?.hid || ''}
        token={data?.attachment?.token || data?.token || ''}>
        <SHeader style={animatedPaddingTop}>
          <FlexCenter>
            <SIconBack onPress={goBack}>
              <Image source={IC_OUTLINED_LEFT} />
            </SIconBack>
            <SAnimateInfo style={animatedTextHeaderStyle}>
              <Avatar size={32} userKey={user_id} />
              <View style={globalStyles.ml8}>
                <STextUser>{user?.name}</STextUser>
                <FlexCenter>
                  {data?.truetype !== 'update' ? (
                    <>
                      <SIconSmall>
                        <SImage
                          source={TypePost.getTypePostImage(
                            data?.truetype || 'update',
                          )}
                        />
                      </SIconSmall>
                      <STextTime
                        style={globalStyles.ml4}>{`${TypePost.getReactionText(
                        data?.truetype || 'update',
                      )}`}</STextTime>
                      <STextDivider> | </STextDivider>
                    </>
                  ) : null}
                  <STextTime>
                    <TimeAgo time={time * 1000} />
                  </STextTime>
                </FlexCenter>
              </View>
            </SAnimateInfo>
          </FlexCenter>
          <SIconOption onPress={openActionUpdate}>
            <Image source={IC_OUTLINED_MORE_HORIZ} />
          </SIconOption>
        </SHeader>
        <Comment.List
          refreshing={refreshing}
          onLoadData={refresh}
          ListHeaderComponent={<PostThumbnail isInFeed={false} id={id} />}
          onScroll={onContentScroll}
          appName={'inside'}
        />
        <Comment.Input />
        <Comment.BottomSheet />
      </Comment.Provider>
    </FullScreenWrapper>
  );
});

const SHeader = styled(Animated.View)`
  background-color: white;
  padding: 8px 16px 8px 0;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
`;
const SIconBack = styled.TouchableOpacity`
  width: 48px;
  height: 48px;
  align-items: center;
  justify-content: center;
`;

const STextUser = styled.Text`
  font-family: ${Fonts.Regular};

  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: #242424;
`;
const STextTime = styled.Text`
  font-family: ${Fonts.Regular};

  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: #666666;
`;
const SAnimateInfo = styled(Animated.View)`
  flex-direction: row;
  align-items: center;
`;
const SIconOption = styled.TouchableOpacity.attrs(p => ({
  hitSlop: {top: 8, bottom: 8, left: 8, right: 8},
}))`
  width: 24px;
  height: 24px;
  align-items: center;
  justify-content: flex-start;
`;
const SIconSmall = styled.View`
  width: 16px;
  height: 16px;
  align-items: center;
  justify-content: center;
`;
const SImage = styled.Image`
  tint-color: #666666;
  width: 16px;
  height: 16px;
`;
export default DetailPostScreen;
